<template>
  <div>
    <el-container>
      <el-header style="text-align: right">
        <el-dropdown @command="logout">
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click="logout">注销</el-dropdown-item>
          </el-dropdown-menu> </el-dropdown
        >{{ this.student.name }}, 你好!</el-header
      >
      <el-container>
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu :default-openeds="['1']" router="true">
            <el-submenu index="1">
              <template slot="title"
                ><i class="el-icon-message"></i>菜单</template
              >
              <el-menu-item-group>
                <el-menu-item index="/student_homepage_personal_info"
                  >个人信息</el-menu-item
                >
                <el-menu-item index="/student_course_choose_to_learn"
                  >课程学习</el-menu-item
                >
                <el-menu-item index="/student_course_select"
                  >选择课程</el-menu-item
                >
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>

        <el-main>
          <el-button type="text" @click="dialogFormVisible = true"
            >修改信息</el-button
          >
          <el-descriptions title="用户信息">
            <el-descriptions-item label="用户ID">{{
              this.student.id
            }}</el-descriptions-item>
            <el-descriptions-item label="班级">{{
              this.student.className
            }}</el-descriptions-item>
            <el-descriptions-item label="姓名">{{
              this.student.name
            }}</el-descriptions-item>
            <el-descriptions-item label="年级">
              {{ this.student.grade }}
            </el-descriptions-item>
            <el-descriptions-item label="电话">
              {{ this.student.phone }}
            </el-descriptions-item>
          </el-descriptions>
        </el-main>
      </el-container>
    </el-container>

    <el-dialog title="修改信息" :visible.sync="dialogFormVisible">
      <el-form :model="tempstudent" >

        <el-form-item label="年级">
          <el-input v-model="tempstudent.grade" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="qq">
          <el-input v-model="tempstudent.qq" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="班级">
          <el-input
            v-model="tempstudent.className"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="updateStudent">提交更新</el-button>
        <el-button @click="dialogFormVisible = false">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
  
  <script>
import axios from "axios";

export default {
  data() {
    return {
      dialogFormVisible: false,
      tempstudent: {
        studentID: localStorage.getItem("id"),
        phone: "",
        grade: "",
        className: "",
      },
      student: {
        studentID: localStorage.getItem("id"),
        name: "",
        phone: "",
        grade: "",
        className: "",
      },
      token: localStorage.getItem("token"),
    };
  },
  methods: {
    logout() {
      console.log("进行注销");
      this.$confirm("确认注销?")
        .then(() => {
          localStorage.removeItem("id");
          localStorage.removeItem("token");
          localStorage.removeItem("name");
          this.$router.push("/");
        })
        .catch(() => {

        });
    },
    updateStudent() {
      console.log(this.tempstudent.name);
      axios
        .post(
          "http://localhost:8080/SongGuo_Learning/student/updateInfo",
          this.tempstudent,
          {
            headers: {
              token: this.token,
            },
          }
        )
        .then((resp) => {
          if (resp.data.message == "UPDATE_OK") {
            this.student.className = this.tempstudent.className;
            this.student.grade = this.tempstudent.grade;
            this.$notify.success({
              title: "修改成功",
              message: "你的个人信息已修改成功!",
            });
          } else {
            this.$notify.error({
              title: "修改失败",
              message: "发生未知错误!",
            });
          }
          this.dialogFormVisible = false;
        });
    },
  },
  mounted() {
    let token = localStorage.getItem("token");
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/student/getInfo?id=" +
          localStorage.getItem("id"),
        {
          headers: {
            token: token,
          },
        }
      )
      .then((resp) => {
        if (resp.data.message == "TOKEN_IS_NULL") {
          this.$router.push("/studentlogin");
          return;
        } else {
          this.student.id = resp.data.data.studentID;
          this.student.name = resp.data.data.name;
          this.student.phone = resp.data.data.phoneNumber;
          this.student.grade = resp.data.data.grade;
          this.student.className = resp.data.data.className;
          localStorage.setItem("name", this.student.name);
          localStorage.setItem("id", this.student.studentID);
        }
      });
  },
};
</script>
  
  <style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}
</style>